



















import { Component, Vue, Prop } from 'vue-property-decorator'
import HelloWorld from '@/components/HelloWorld.vue' // @ is an alias to /src
import Page from '@/components/common-components/page.vue'
import { getFleetList } from '@/store/modules/bus/actions'
@Component({
  components: {
    HelloWorld,
    Page
  }
})
export default class carInfo extends Vue {
  $createToast : any
  carList = []
  // 声明周期钩子
  page = 1
  limit = 10
  count = 0
  carId : any = ''
  carNums = 0
  carInfo : any = {}
  activated () {
    this.carId = this.$route.query.carId
    this.getCarInfo({})
  }
  deactivated () {
    this.carList = []
    this.page = 1
    this.count = 0
    this.carInfo = {}
  }

  @Prop({ default: 'default value' }) readonly propB!: string

  // 计算属性
  get showMore () {
    return Math.ceil(this.count / this.limit) >= (this.page + 1)
  }
  get fleetId () {
    return this.$route.query.fleetId
  }
  getCarInfo (obj : any) {
    let skip = obj.skip || 0
    let limit = obj.limit || 10
    let carId = this.carId
    const toast = this['$createToast']({
      time: 0,
      txt: '加载...'
    })
    toast.show()
    this.$store.dispatch('getCarInfo', { carId: carId }).then((response) => {
      if (response.success) {
        this.carNums = response.data.nums
        this.carInfo = response.data.carInfo
      }
      toast.hide()
    })
  }
  pageTurning () {
    if (!this.showMore) {
      return
    }
    this.page++
    let skip = (this.page - 1) * this.limit
    this.getCarInfo({ skip: skip })
  }
  goTo (type : any) {
    this.$router.push({ path: '/' + type, query: { carId: this.carId, fleetId: this.fleetId, carInfo: this.carInfo } })
  }
}
